<template>
	<view class="container">
		<!-- 猜你喜欢 begin -->
		
		<view class="you-may-also-like">
			<navigator class="product" v-for="(product, index) in mart" :key="index" open-type="navigate" hover-class="none" url="/pages/mart/detail">
				<image :src="product.img" mode="widthFix"></image>
				<view class="info">
					<view class="desc">
						<view class="title">{{ product.name }}</view>
						<!-- <view class="sub">{{ product.itemSalesVolume }}人已购买</view> -->
					</view>
					<view class="extra">
						<view class="price"><button class="bu" type="default" hover-class="none" @tap="reshape(product.name,product.img,product.price,product.description,product.diskid)">修改信息</button>	</view>
						
						<view class="action">
													
							<button class="bu" type="primary" hover-class="none" @tap="del":data-newsid=product.name  >删除</button>
							<!-- <view class="title" @tap="del":data-newsid=product.name>删除</view> -->
							<!-- <view class="subtitle">NEW ARRIVALS</view>
							<image src="/static/images/common/closex.png"></image> -->
						</view>
					</view>
				</view>
			</navigator>
		</view>
		<!-- 猜你喜欢 end -->
		
		<nomore text="已经到底了,去其他页面逛逛吧" visible></nomore>
	</view>
</template>

<script>
	import nomore from '@/components/nomore/nomore.vue'
	
	export default {
		components: {
			nomore
		},
		data() {
			return {
				mart: [],
				messge:""
			}
		},
		async onLoad() {
			uni.request({
				url: 'http://8.142.82.195/viewtodo',
				method: 'GET',
				data: {},
				success: res => {this.mart=res.data.data;},
				fail: () => {},
				complete: () => {}
			})

		},
		 onShow:function(){
			uni.request({
				url: 'http://8.142.82.195/viewtodo',
				method: 'GET',
				data: {},
				success: res => {this.mart=res.data.data;},
				fail: () => {},
				complete: () => {}
			})
			
		},
		methods:{
			//删除菜品
			del(e){
				var name=e.currentTarget.dataset.newsid;

				uni.request({
					url: 'http://8.142.82.195/deleteMenu',
					method: 'GET',
					data: {'diskname':name},
					success: res => {
					uni.request({
					url: 'http://8.142.82.195/viewtodo',
					method: 'GET',
					data: {},
					success: res => {this.mart=res.data.data;this.messge=res;},
					fail: () => {},
					complete: () => {}
				});
				
				},
					fail: () => {uni.showToast({
						title: '网络错误！！',
						duration: 2000
					});},
					complete: () => {}
				});
				
				uni.showToast({
					title: '删除成功！！',
					duration: 2000
				});

			},
			reshape(name,img,price,description,diskid){
				// console.log(name);
				console.log(diskid)
				uni.navigateTo({
					url: '/pages/menu/menu?name='+name+"&img="+img+"&price="+price+"&description="+description+"&diskid="+diskid,
					success: res => {},
					fail: () => {},
					complete: () => {}
				});
				
			}
		}
	}
</script>

<style lang="scss">
	page {
		height: auto;
		
		/* #ifdef H5 */
		min-height: 100%;
		/* #endif */
	}
	
	.banner-swiper {
		height: 750rpx;
		
		.swier-item {
			image {
				height: 100%;
			}
		}
	}
	
	.banner-section {
		display: flex;
		background-color: #FFFFFF;
		
		image {
			width: 375rpx;
		}
	}
	
	.section-header {
		display: flex;
		align-items: baseline;
		padding: 20rpx 0;
		.title {
			font-family: 'wenyue';
			font-size: 44rpx;
			margin-right: 20rpx;
		}
		
		.subtitle {
			font-family: 'neutra';
			font-size: $font-size-sm;
			color: $text-color-assist;
		}
	}
	
	.content-section {
		background-color: #FFFFFF;
		padding: 0 30rpx;
	}
	
	.new-arrivals {
		margin-bottom: 20rpx;
		.section-body {
			display: flex;
			flex-direction: column;
			
			image {
				width: 100%;
				margin-bottom: 10rpx;
			}
		}
	}
	
	.you-may-also-like {
		padding: 20rpx 30rpx;
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
		
		.product {
			background-color: #FFFFFF;
			width: 335rpx;
			display: flex;
			flex-direction: column;
			border-radius: $border-radius-lg;
			margin-bottom: 20rpx;
			
			image {
				width: 100%;
				border-radius: $border-radius-lg $border-radius-lg 0 0;
			}
			
			.info {
				padding: 20rpx;
				display: flex;
				flex-direction: column;
				overflow: hidden;
				
				.desc {
					display: flex;
					flex-direction: column;
					
					.title {
						font-size: $font-size-lg;
						overflow: hidden;
						text-overflow: ellipsis;
						white-space: nowrap;
						margin-bottom: 14rpx;
					}
					
					.sub {
						font-size: $font-size-sm;
						color: $text-color-assist;
					}
				}
				
				.extra {
					margin-top: 20rpx;
					display: flex;
					align-items: center;
					justify-content: space-between;
					
					.price {
						color: $color-primary;
						font-size: $font-size-lg;
						font-weight: 500 !important;
					}
					
					.action {
						image {
							width: 40rpx;
							height: 40rpx;
						}
					}
				}
			}
		}
	}
</style>
